.profile__avatar{
    width: 120px;
    height: 120px;
}

@media screen and (max-width: 768px) {
    .profile__avatar{
        width: 100px;
        height: 100px;
    }
}

@media screen and (max-width: 320px) {
    .profile__avatar{
        width: 75px;
        height: 75px;
    }
}